import React from "react";
import Weather from "./components/weather";
import Form from "./components/form";
// import SWeather from "./components/stateless_weather";
// import SForm from "./components/stateless_form"
import Titles from "./components/titles";

const Api_Key = "9e7dc0060af1a6f73cb1c840815a9f14";

class App extends React.Component {

  state = {
    //Search temps
    temperature: undefined,
    city: undefined,
    country: undefined,
    humidity: undefined,
    description: undefined,
    error: undefined,
    //CSUN temps
    csunTitle: undefined,
    csunTemp: undefined,
    csun: undefined,
    usa: undefined,
    csunHumidity: undefined,
    csunDescription: undefined,
    //Current temps
    currentTitle: undefined,
    currentTemp: undefined,
    currentLat: undefined,
    currentLon: undefined,
    currentCity: undefined,
    currentCountry: undefined,
    currentHumidity: undefined,
    currentDescription: undefined
  }

  componentDidMount(){
	this.getLocation();
  }

  getLocation(){
	 navigator.geolocation.getCurrentPosition(
    (position) => {
        this.setState(
        (prevState) => ({
            currentLat: position.coords.latitude,
            currentLon: position.coords.longitude
            }), () => { this.getWeather(); }
        );
    },
        (error) => this.setState({ error: error.message }),
        { enableHighAccuracy: true, timeout: 20000, maximumAge: 1000 },
    );
  }

  //getWeather is a method we'll use to make the api call
  getWeather = async (e) => {
  try{
    const city = e.target.elements.city.value;
    const country = e.target.elements.country.value;
    e.preventDefault();
	//Get searched temps info from API
    const api_call = await fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${city},${country}&appid=${Api_Key}&units=imperial`
    );
    const response = await api_call.json();
	//Get Northridge temps info from API
    const api_call2 = await fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=northridge,usa&appid=${Api_Key}&units=imperial`
    );
    const response2 = await api_call2.json();
	//Get current position temps info from API
    const api_call3 = await fetch(
      `https://api.openweathermap.org/data/2.5/weather?lat=` + this.state.currentLat + `&lon=` + this.state.currentLon + `&appid=${Api_Key}&units=imperial`
    );
    const response3 = await api_call3.json();
 
    console.log(response);
    console.log(response2);
    console.log(response3);
    if (city && country) {
      this.setState({
        temperature: response.main.temp,
        city: response.name,
        country: response.sys.country,
        humidity: response.main.humidity,
        description: response.weather[0].description,
        error: "",
        csunTitle: "CSUN Weather",
        csunTemp: response2.main.temp,
        csun: response2.name,
        usa: response2.sys.country,
        csunHumidity: response2.main.humidity,
        csunDescription: response2.weather[0].description,
	currentTitle: "Current location weather",
	currentTemp: response3.main.temp,
	currentCity: response3.name,
	currentCountry: response3.sys.country,
	currentHumidity: response3.main.humidity,
	currentDescription: response3.weather[0].description
      });
    } else {
      this.setState({
        error: "Please input search values..."
      });
    }
  } catch(err) {
	this.setState({
        error: "Please input valid city and country..."
      });
  }
  }

  render() {

    return (
	
	<div>
        <div className="wrapper">
          <div className="main">
            <div className="container">
              <div className="row">
                <Titles />
                <div
                  className="form-container2"
                >
                  <Form loadWeather={this.getWeather} />
                  <Weather
                    temperature={this.state.temperature}
                    city={this.state.city}
                    country={this.state.country}
                    humidity={this.state.humidity}
                    description={this.state.description}
                    error={this.state.error}
                    title={this.state.csunTitle}
                    csunTemp={this.state.csunTemp}
                    csun={this.state.csun}
                    usa={this.state.usa}
                    csunHumidity={this.state.csunHumidity}
                    csunDescription={this.state.csunDescription}
	            currentTitle={this.state.currentTitle}
                    currentTemp={this.state.currentTemp}
                    currentCity={this.state.currentCity}
                    currentCountry={this.state.currentCountry}
                    currentHumidity={this.state.currentHumidity}
                    currentDescription={this.state.currentDescription}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    )
  }
}
export default App;



