import React from "react";

class Titles extends React.Component {

    render() {

        return (

            <div>

                <h1 className="title-container__title">What's the Weather Like Today?</h1>
                <p className="title-container__subtitle">Get informed about the weather conditions of any city worldwide ...</p>
            </div>
        )
    }
}

export default Titles;


